import React, { Component } from 'react';

class Home extends Component {
    state = {}
    StyleContainerHome = {
        fontFamily: 'Roboto Mono',
        marginLeft: "10vw",
        width: "90vw",
        height: "50vh"
    }

    render() {
        return (
            <div id="home" style={this.StyleContainerHome}>
                <div style={{ position: "relative", top: "10vh", margin: "10vw auto 0" }}>
                    <h6 className="text-light">I'M RIAN AHMAD</h6>
                    <h1 className="text-light">ENTHUSIAST</h1>
                    <h1 className="text-light">WEB DEVELOPER</h1>
                </div>
            </div>
        );
    }
}

export default Home;